.phone-outline {
    background-color: white;
    background-image: linear-gradient(127deg, blue,white);
    border:solid black;
    display:flex;
    height:80vh;
    width:40vh;
    position: absolute;
    right:40%;
    border-radius: 25px;
    top:10%;
}
#phone-camera {
    height: 6vh;
    width:20vh;
    position: absolute;
    right:26%;
    border-radius:0px 0px 7px 7px;
    background-color:black;
}
#logo {
    background-color: 'ngpro-logo';
    border-radius: 30%;
    height:15vh;
    width:15vh;
    position:absolute;
    right:35%;
    top:15%;
}
#password-box {
    background-color:white;
    border-radius:10px;
    border:solid;
    height: 10vh;
    width:30vh;
    position: absolute;
    right: 10.5%;
    top:40%;
}
.p-input {
    font-size:60px;
    /* -webkit-text-security: disc; */
}
#submit {
    border-radius:10px;
    height:10vh;
    width:20vh;
    position: absolute;
    right:25%;
    top:58%;
    border-style: none;
    font-size:30px;
}
